@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap);
:root {
  --body-font: 'Cabin', sans-serif;
  --subheading-font: 'Lobster Two', cursive;
  --heading-font: 'Lobster', cursive;
  --btn-color: #26AA95;
}

.flagship-btn {
  background-color: #26AA95;
  font-family: 'Cabin', sans-serif;
  font-family: var(--body-font);
  font-weight: 700;
  width: 175px;
  height: 50px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15vh;
    position: relative;
    left: 20px;
}

.header-title {
    font-family: 'Lobster', cursive;
    font-size: 4rem;
    letter-spacing: 0.15rem;
    position: relative;
    top: 5px;
}

.header-leaf {
    height: 65px;
}

.registration-label {
    font-family: var(--body-font);
    width: 100%;
    text-align: left;
    font-weight: 500;
}

.login-label {
    width: 100%;
    text-align: left;
}

.splash-container {
    font-family: var(--body-font);
    width: 950px;
    margin: auto;
    height: 70vh;
}

.splash-main-heading {
    font-size: 3.25rem;
    padding-bottom: 0.25rem;
}

.splash-sub-heading {
    font-size: 1.5rem;
    padding-bottom: 1rem;
}

.flourish-span {
    color: #1c917d;
}

.splash-description {
    font-size:0.95rem;
    line-height: 2rem;
}

.splash-list {
    list-style: none;
    text-align: left;
    margin: auto;
}

.splash-lobster-font {
    font-family: var(--subheading-font);
    font-size: 1.15rem;
}



.splash-soil {
    color:#26aa95;
    font-weight: 900;
}
.splash-vegetables {
    color:#229986;
    font-weight: 900;
}
.splash-companion {
    color:#1E8877;
    font-weight: 900;
}
.splash-layout {
    color:#1A7768;
    font-weight: 900;
}
.splash-summary {
    color:#166659;
    font-weight: 900;
}

.splash-list-step {
    font-size: 0.85rem;
}


.splash-btn-group {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 400px;
    margin: auto;
    padding-top: 2rem;

}




.soil-container {
 overflow: scroll;
 height: 75vh;
}

.map-view {
    width: 80%;
    height: 350px;
    cursor: pointer;
    margin: 0 auto;
  }

.esri-view-surface canvas {
    border: 5px solid var(--btn-color);
    
}

  .esri-icon-font-fallback-text {
      display: inline-block;
      font-weight: 900;
      color: white;
      background-color: var(--btn-color);
      font-family: var(--body-font);
      width: 100px;
    height: 100%;
    margin-left: 0.25rem;
    border: 2px solid var(--btn-color);
  }

  .esri-icon-font-fallback-text:hover {
      background-color: white;
      border: 2px solid var(--btn-color);
      color: var(--btn-color);
  }

  .esri-ui-corner {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 1rem;
  }



.esri-attribution__sources, .esri-attribution__powered-by {
    display: none;
}

.display-soil-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 5px solid darkgreen;
    width: 200px;
    height: 50px;
    margin: auto;
    position: relative;
    top: 100px;
    font-weight: 900;
}

.display-soil-p {
    margin-bottom: 0;
}

.display-results-container {
    width: 750px;
    margin: auto;
    position: relative;
    top: 125px;
}

#soil-save-btn {
    position: relative;
    top: 125px;
}

.navbar-container {
    height: 5vh;
}

.navbar-radio-btn {
    width: 200px;
    color: var(--btn-color);
    font-weight: 600;
    border: none;
}

.nav-btn:hover {
    background-color: var(--btn-color);
}

.nav-btn:active {
    background-color: var(--btn-color);
}
.btn-check:checked+.btn-outline-success {
    background-color: var(--btn-color);
    border-color: var(--btn-color);
}

.nav-btn-group {
    border: 1px solid var(--btn-color);
    width: 800px;
    margin: auto;
    border-radius: 0.5rem;
}

.footer-logo-link {
    text-decoration: none;
    color: darkgreen;
}

.footer-container {
    font-family: var(--body-font);
    letter-spacing: 0.05rem;
}
